import {
  useLocation,
  useNavigate,
  useParams,
  useSearchParams,
} from "react-router-dom";
import InstituteDepartmentClassStudent from "../../InstituteMember/InstituteAcademicDepartment/InstituteDepartmentClass/InstituteDepartmentClassStudent/InstituteDepartmentClassStudent";
import InstituteDepartmentTheoryClass from "../../InstituteMember/InstituteAcademicDepartment/InstituteDepartmentClass/InstituteDepartmentTheoryClass/InstituteDepartmentTheoryClass";
import InstituteDepartmentPracticalBatchClass from "../../InstituteMember/InstituteAcademicDepartment/InstituteDepartmentClass/InstituteDepartmentPracticalBatchClass/InstituteDepartmentPracticalBatchClass";
import HorizontalTabWrapper from "../../Ui/Tab/Horizontal/HorizontalTabWrapper";
import HorizontalTabCard from "../../Ui/Tab/Horizontal/HorizontalTabCard";
import UserMemberContentWrapper from "../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../Ui/Border/BorderBottom";
import style from "../FinanceManager/FinanceManager.module.css";
import { assestsUtilityUrl } from "../../Services/UrlConfig/AssestsBaseUrl";
import { useCallback } from "react";
import InstituteDepartmentClassDseStudent from "../../InstituteMember/InstituteAcademicDepartment/InstituteDepartmentClass/InstituteDepartmentClassDseStudent/InstituteDepartmentClassDseStudent";
const AcademicDepartmentManagerClass = ({ instituteId }) => {
  const getQuery = useLocation();
  const params = useParams();
  const [searchParams] = useSearchParams();
  const navigate = useNavigate();

  const onNavigate = useCallback(() => {
    let url = "";
    url = `/q/${params.username}/member/staff/academic/department/${params.name}?type=cls`;
    navigate(url, {
      state: getQuery.state,
    });
  }, [navigate, getQuery.state, params.username]);
  return (
    <>
      <UserMemberContentWrapper>
        <div className={style.fm_header_container}>
          <div
            className={style.fm_header_start_container}
            style={{
              gap: "0.2rem",
            }}
          >
            <img
              alt="search icon"
              src={`${assestsUtilityUrl}/back.svg`}
              style={{
                height: "2rem",
                width: "2rem",
              }}
              onClick={onNavigate}
            />
            <h6>{getQuery.state?.clsName ?? ""}</h6>
          </div>
        </div>
      </UserMemberContentWrapper>
      <BorderBottom />
      <UserMemberContentWrapper
        customStyle={{
          paddingTop: "0",
        }}
      >
        <HorizontalTabWrapper>
          {tabList.map((tab) => (
            <HorizontalTabCard
              key={tab?.id}
              tab={tab}
              isLink
              isActive={
                tab.id === "students" && !searchParams.get("n")
                  ? true
                  : searchParams.get("n") === tab.id
                  ? true
                  : false
              }
              parentState={getQuery.state}
              url={`/q/${params.username}/member/staff/academic/department/${params.name}?type=cls-detail${tab.urlKey}`}
            />
          ))}
        </HorizontalTabWrapper>
        <BorderBottom
          customStyleBorder={{
            marginTop: "0",
          }}
        />
      </UserMemberContentWrapper>
      {!searchParams.get("n") ? (
        <InstituteDepartmentClassStudent
          clsId={getQuery.state?.clsId}
          openAs="DEPARTMENT_HEAD"
        />
      ) : searchParams.get("n") === "dse" ? (
        <InstituteDepartmentClassDseStudent
          clsId={getQuery.state?.clsId}
          openAs="DEPARTMENT_HEAD"
        />
      ) : searchParams.get("n") === "theory" ? (
        <InstituteDepartmentTheoryClass
          clsId={getQuery.state?.clsId}
          instituteId={instituteId}
          openAs="DEPARTMENT_HEAD"
        />
      ) : searchParams.get("n") === "practical" ? (
        <InstituteDepartmentPracticalBatchClass
          clsId={getQuery.state?.clsId}
          instituteId={instituteId}
          openAs="DEPARTMENT_HEAD"
        />
      ) : null}
    </>
  );
};

export default AcademicDepartmentManagerClass;

const tabList = [
  {
    label: "normal_students",
    id: "students",
    urlKey: "",
  },
  {
    label: "dse_student",
    id: "dse",
    urlKey: "&n=dse",
  },
  {
    label: "theory_classes",
    id: "theory",
    urlKey: "&n=theory",
  },
  {
    label: "practical_batches",
    id: "practical",
    urlKey: "&n=practical",
  },
];
